import React, { Component } from 'react';
import styled from 'styled-components';
import Text from '../Layout/Text';
import Flex from '../Layout/Flex';
import Box from '../Layout/Box';
import Container from '../Layout/Container';
import OwlCarousel from 'react-owl-carousel2';
import '../css/styles.css';
import '../css/owl.carousel.css';
import '../css/owl.theme.default.css'



const Absolute = styled(Flex)`
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
`;

const InnerImage = styled(Flex)`
  position: absolute;
  top: 0; right: 0; bottom: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: auto;
  height: auto;
  height: 620px;
  padding-bottom: 5px;
`;

const PancakeDetails = styled(Box)`
  position: absolute;
  top: 70px; left: 0px; right: 0px; bottom: 0;
  display: flex;
  padding: 0 100px;
  justify-content: flex-start;
  flex-direction: column;
`;

const TastyText = styled(Text)`
  text-transform : capitalize
  letter-spacing : 1.2px;  
`;

const CapitalText = styled(Text)`
  text-transform: uppercase;
  letter-spacing: 1.4px;
  font-weight: bold;
`;

const Capital = styled(Text)`
  text-transform: uppercase;
`;

const options = {
  items: 3,
  nav: true,
  // rewind: true,
  autoplay: true,
  margin: 20,
  dots: true,
  dotsEach: true,
  navText : ["<img src='https://website.s3.nl-ams.scw.cloud/left-arrow.png' />","<img src='https://website.s3.nl-ams.scw.cloud/right-arrow.png' />"]
};

const WindImgBox = styled(Box)`
  position: Absolute;
  bottom: 0px;
  right: 0px;
`;

const TimeBox = styled(Flex)`
  top : -120px;
  right: -120px;
  position: absolute;
  box-shadow: -5px 20px 40px 5px #c6c6c66b;
`;

const AbsBox = styled(Box)`
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
`;
const StyleBox = styled(Box)`
  bottom: -12px;
`;
export default class pancakes extends Component {
  render() {
    return (
      <StyleBox  position="relative">
        <img width="100%" src="https://website.s3.nl-ams.scw.cloud/third-section-bg-jpg.png"/>
        <Absolute>
          <Container>
            <Box py={0} position="relative">
              <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-jpg.png" width="100%"/>
              <InnerImage>
                <Box position="relative" width="100%" height="100%">
                  <img height="100%" src="https://website.s3.nl-ams.scw.cloud/text-bg-jpg.png"/>
                  <PancakeDetails>
                    <TimeBox height={200}>
                      <Box position="relative">
                        <img src="https://website.s3.nl-ams.scw.cloud/ready-in-jpg.png" />
                        <AbsBox>
                          <Capital color="#a47d5e" fontSize={18} fontWeight="bold" letterSpacing="1.8px" lineHeight="30px"> ready in </Capital>
                          <Text color="#dfc691" fontSize={92} fontWeight="bold" letterSpacing="6px" lineHeight={0.8}> 40 </Text>
                          <Text textAlign="center" color="#979285" fontSize={18} fontWeight="bold" letterSpacing="2px" lineHeight="30px" pt={2}> mins </Text>
                        </AbsBox>
                      </Box>
                      {/* <Box>
                        
                      </Box> */}
                    </TimeBox>
                    <TastyText fontSize={48} fontWeight="bold" color="#56534c"> tasty pancakes </TastyText>
                    <CapitalText pt={2} color="#a47d5e" fontSize={14}> season favourite</CapitalText>
                    <Text color="#56534c" letterSpacing="1.4px" py={5} pr={5} lineHeight={2}>
                      Maecenas nec odio et ante tincidunt tempus. Donec vitae sapien ut libero venenatis faucibus. Nullam quis ante. Etiam sit amet orci eget eros faucibus.
                    </Text>
                    <Flex>
                      <img src="https://website.s3.nl-ams.scw.cloud/rating.png" />
                    </Flex>
                    <Box width={410} height={150} pt={"30px"} pl={"34px"}>
                      <OwlCarousel options={options}>
                        <Box m={"13px"}>
                          <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-3-jpg.png" alt="The Last of us"/>
                        </Box >
                        <Box m={"13px"}>
                          <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-2-jpg.png" alt="GTA V"/>
                        </Box >
                        <Box m={"13px"}>
                          <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-1-jpg.png" alt="Mirror Edge"/>
                        </Box >
                        <Box m={"13px"}>
                          <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-3-jpg.png" alt="The Last of us"/>
                        </Box >
                        <Box m={"13px"}>
                          <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-2-jpg.png" alt="GTA V"/>
                        </Box >
                        <Box m={"13px"}>
                          <img src="https://website.s3.nl-ams.scw.cloud/tasty-pancakes-1-jpg.png" alt="Mirror Edge"/>
                        </Box >
                      </OwlCarousel>
                    </Box>
                    <WindImgBox width={320}>
                      <img width="100%" src="https://website.s3.nl-ams.scw.cloud/vektor-smartobjekt-windmill-jpg.png"/>
                    </WindImgBox>
                  </PancakeDetails>
                </Box>
              </InnerImage>
            </Box>
          </Container>
        </Absolute>
      </StyleBox>
    )
  }
}


